import React, { Component } from 'react';
import {
  Card,
} from 'antd';
import Page from 'components/LayoutComponents/Page';
import TableGradeStudentView from './TableGradeStudentView';

class ClassOwnerActivity extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <Card title="Xem điểm sinh viên" className="class-owner-activity">
        <TableGradeStudentView />
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassOwnerActivity />
  </Page>
);



